@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono:ital,wght@0,400;0,700;1,400&display=swap);
body{margin:0;font-family:"Ubuntu Mono",monospace;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:20px;line-height:1.3;background-color:#edf0f5}.map-container{width:100%;height:100%;position:absolute}.map-container-location{width:100%;max-width:100%;height:300px}#hidden{display:none}.title{position:absolute;top:10px;left:10px}@media(max-width: 600px){.title{width:60%}}.title h1{font-size:22px;margin:0px;padding:0 5px;background-color:#c7e2f0;display:inline}.title-about{position:absolute;top:10px;right:10px;font-size:20px;text-align:right}.title-about a{color:#000;margin-bottom:8px;display:inline;margin-left:8px}.title-about a span{background-color:#c7e2f0;padding:1px 4px}@media(max-width: 600px){.title-about a{display:block;margin-left:0px}}.signup{margin:15px}.signup h1{margin-top:0px}.signup p{margin-bottom:15px}.signup form{display:flex;flex-direction:row;flex-wrap:wrap;max-width:600px;align-items:flex-start;justify-content:space-between}.signup label{margin-bottom:20px;max-width:275px;width:100%}@media(max-width: 600px){.signup label{max-width:100%}}.signup input,.signup .mapboxgl-ctrl-geocoder{display:block;width:calc(100% - 8px);margin-top:2px;font-size:20px;font-family:"Ubuntu Mono",monospace;box-shadow:none;border:2px solid #ccc;height:36px}.signup input input,.signup .mapboxgl-ctrl-geocoder input{border-width:0px;margin-top:0px;height:100%;width:100%}.signup input.submit,.signup .mapboxgl-ctrl-geocoder.submit{max-width:275px;font-family:"Ubuntu Mono",monospace;font-weight:700;height:43px;margin-top:20px}@media(max-width: 600px){.signup input.submit,.signup .mapboxgl-ctrl-geocoder.submit{max-width:100%;width:100%}}.signup .mapboxgl-ctrl-geocoder{height:44px;border-radius:0px;width:100%}.signup .mapboxgl-ctrl-geocoder--icon-search{top:9px}.signup .mapboxgl-ctrl-geocoder--icon-close{margin-top:3px}@media screen and (min-width: 640px){.signup .mapboxgl-ctrl-geocoder{line-height:1;max-width:none;max-width:initial}.signup .mapboxgl-ctrl-geocoder--input{padding:0px 15px 0 35px}.signup .mapboxgl-ctrl-geocoder--icon-search{top:9px}.signup .mapboxgl-ctrl-geocoder--icon-close{margin-top:5px}}.signup .range-label{position:absolute;right:0px;display:inline}.success{margin:15px}.success a{margin-left:5px;color:#000}.about{margin:15px;max-width:800px}.about a{color:#000}.about span{top:-5px;position:relative}.about .note{font-size:16px;font-style:italic}
